body {
  font-family: "Ubuntu", sans-serif;
  background: var(--background-color);
  color: var(--text-color);
  font-weight: var(--font-weight);
  transition: background 0.3s ease-in-out, color 0.6s ease-in-out;
}







#darkMode {
  background: transparent url(/job-search/static/media/moon.08c8560a.png) no-repeat center;
  background-size: 30px 30px;
  width: 45px;
  height: 45px;
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
  border: none;
  border-radius: 50%;
  transition: background-color 0.3s ease-in-out, -webkit-filter 0.3s ease-in-out;
  transition: background-color 0.3s ease-in-out, filter 0.3s ease-in-out;
  transition: background-color 0.3s ease-in-out, filter 0.3s ease-in-out, -webkit-filter 0.3s ease-in-out;
}

#darkMode:hover,
#darkMode:focus {
  -webkit-filter: none;
          filter: none;
  background-color: black;
  cursor: pointer;
}

#darkMode.clicked {
  -webkit-filter: none !important;
          filter: none !important;
  background-color: black;
}

body.light {
  --background-color: var(--white);
  --text-color: var(--dark);
  --font-weight: 400;
  --image: url(/job-search/static/media/day.c909d341.jpg);
}

body.dark {
  --background-color: var(--dark);
  --text-color: var(--white);
  --font-weight: 500;
  --image: url(/job-search/static/media/night.1e76205c.jpg);
}

.type,.url {
    text-decoration: none;
    color: black;
    font-size: medium;
}



#url {
    color: blue;
    font-size: medium;
}

.details {
    color: black;
}
