#darkMode {
  background: transparent url("../../assests/moon.png") no-repeat center;
  background-size: 30px 30px;
  width: 45px;
  height: 45px;
  filter: grayscale(100%);
  border: none;
  border-radius: 50%;
  transition: background-color 0.3s ease-in-out, filter 0.3s ease-in-out;
}

#darkMode:hover,
#darkMode:focus {
  filter: none;
  background-color: black;
  cursor: pointer;
}

#darkMode.clicked {
  filter: none !important;
  background-color: black;
}

body.light {
  --background-color: var(--white);
  --text-color: var(--dark);
  --font-weight: 400;
  --image: url("../../assests/day.jpg");
}

body.dark {
  --background-color: var(--dark);
  --text-color: var(--white);
  --font-weight: 500;
  --image: url("../../assests/night.jpg");
}
